import { Component, OnInit, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit {
  title = 'awesome';
  loading = true;
  selected = {
    fa: 'fa fa-user',
    text: 'loading punches'
  };
  list = [
    {
      fa: 'fa fa-user',
      text: 'loading punches'
    },
    {
      fa: 'fas fa-utensils',
      checked: true,
      text: 'All'
    },
    {
      fa: 'fas fa-hamburger', text: 'Ham'
    },
    { fa: 'fas fa-ice-cream', text: 'Ice cream' },
    { fa: 'fas fa-pizza-slice', text: 'pizza' },
    { fa: 'fas fa-drumstick-bite', text: 'chicken' },
    { fa: 'fas fa-utensils', text: 'Rice' },
    { fa: 'fas fa-bread-slice', text: 'Bread' },
    { fa: 'fas fa-cheese', text: 'Cheese' },
    { fa: 'fas fa-egg', text: 'Egg' }
  ];
  noRef = 0;
  animationDuration = 0;
  ngOnInit() {
    const interval = this.animationDuration / this.list.length;
    setInterval(() => {
      this.selected = this.list[this.noRef];
      this.noRef++;
    }, interval);
    setTimeout(() => {
      this.loading = false;
    }, this.animationDuration);


  }
  ngAfterViewInit() {


  }
}
